



















































































import { Vue, Component, Inject, Prop, Watch } from 'vue-property-decorator'
import Dialog from './Dialog.vue'
import FieldContainer from './FieldContainer.vue'
import DropdownField from './DropdownField.vue'
import TextField from './TextField.vue'
import EntitySearchBar from './EntitySearchBar.vue'
import EditorFieldGroups from './EditorFieldGroups.vue'
import EditorFieldGroup from './EditorFieldGroup.vue'
import EditorField from './EditorField.vue'
import TabBar from './TabBar.vue'
import DropdownMenuDivider from './DropdownMenuDivider.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import DiagramVue from './Diagram.vue'
import { Entity, EntityDefinition, EntityType, Field, RelationDefinition, RelationType } from '@/diagram'
import _set from 'lodash.set'

@Component({
  components: {
    Dialog,
    FieldContainer,
    DropdownField,
    TextField,
    EntitySearchBar,
    EditorFieldGroups,
    EditorFieldGroup,
    EditorField,
    TabBar,
    DropdownMenuDivider,
    ButtonGroup,
    Button,
  },
})

export default class AddRelationDialog extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: RelationType, required: true })
  readonly relationType!: RelationType

  @Prop({ type: Entity, required: true })
  readonly entity!: Entity

  @Prop({ type: Boolean, required: true })
  readonly asParent!: false

  @Prop(Function)
  readonly onConfirm?: () => void

  @Prop(Function)
  readonly onDismiss?: () => void

  entityFields: (Field & { value: any })[] = []
  relationFields: (Field & { value: any })[] = []

  entityMode: 'search' | 'custom' = 'custom' // 'search'
  entityType: EntityType | null = null

  get diagram () {
    return this.diagramVm.diagram
  }

  get relationTypeLabel () {
    const { labels } = this.relationType
    return (
      (this.asParent ? labels.singularFrom : labels.singularTo)
      ?? labels.singular
    )
  }

  get supportedEntityTypes () {
    const types = new Set<EntityType>()

    for (const support of this.relationType.supports) {
      if (this.asParent) {
        if (support.to !== this.entity.type.id) continue
      } else {
        if (support.from !== this.entity.type.id) continue
      }

      const type = this.diagram.entityTypes.get(this.asParent ? support.from : support.to)
      types.add(type)
    }

    return [ ...types ]
  }

  get entityTypeDropdownOptions () {
    const options: ({ id: string, label: string, value: EntityType })[] = []

    for (const type of this.supportedEntityTypes) {
      options.push({ id: type.id, label: type.labels.singular, value: type })
    }

    return options
  }

  close () {
    this.$emit('close')
  }

  confirm () {
    this.close()
    this.onConfirm?.()

    const generateId = (prefix: string, items: Map<string, any>) => {
      const time = Date.now()
      let i = 0
      let id = ''
      while (!id || items.has(id)) {
        id = `${prefix}-${time}-${i}`
        i++
      }
      return id
    }

    const generateData = (fields: ({
      dataKey?: string,
      value: any,
    })[]) => {
      const data: Record<string, any> = {}
      for (const field of fields) {
        if (field.dataKey) _set(data, field.dataKey, field.value)
      }
      return data
    }

    if (this.entityMode === 'custom') {
      if (!this.entityType) return

      const entity: EntityDefinition = {
        type: this.entityType.id,
        id: generateId('CUSTOM-ENTITY', this.diagram.activeDiagram.data.entitiesMap),
        data: generateData(this.entityFields),
      }

      const relation: RelationDefinition = {
        type: this.relationType.id,
        id: generateId('CUSTOM-RELATION', this.diagram.activeDiagram.data.relationsMap),
        from: this.asParent ? entity.id : this.entity.id,
        to: this.asParent ? this.entity.id : entity.id,
        data: generateData(this.relationFields),
      }

      this.diagram.activeDiagram.data.add({
        entities: [ entity ],
        relations: [ relation ],
      })
    } else {
      // TODO
    }
  }

  dismiss () {
    this.close()
    this.onDismiss?.()
  }

  @Watch('supportedEntityTypes', { immediate: true })
  onSupportedEntityTypesChanged () {
    this.entityType = this.supportedEntityTypes[0]
  }

  @Watch('entityType', { immediate: true })
  onEntityTypeChanged () {
    this.entityFields = (
      this.entityType?.fields
        .filter(field => field.isEntityLabel)
        .map(field => ({
          ...field,
          value: undefined,
          active: true,
        }))
    ) ?? []
  }

  @Watch('relationType', { immediate: true })
  onRelationTypeChanged () {
    this.relationFields = (
      this.relationType.fields
        .filter(field => field.isRelationLabel)
        .map(field => ({
          ...field,
          value: undefined,
          active: true,
        }))
    )
  }
}
