

























































































import { Vue, Component, Model, Ref, Inject, Prop, Watch } from 'vue-property-decorator'
import Diagram, { EntityType } from '@/diagram'
import TextField from './TextField.vue'
import BaseButton from './BaseButton.vue'
import { watchViewport } from '@/util/watch-viewport'
import { watchRect } from '@/util/watch-rect'
import DiagramVue from './Diagram.vue'

type Promisable<T> = T | Promise<T>

type Cache = Map<string, Promisable<Suggestion[]>>

interface Suggestion {
  id: string
  name: string
}

const caches = new Map<Diagram, Cache>()

@Component({
  components: {
    TextField,
    BaseButton,
  },
})

export default class EntitySearchBar extends Vue {
  log = console.log

  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop(EntityType)
  readonly entityType?: EntityType

  @Model('change', String)
  readonly entityId?: string

  @Ref()
  readonly textField!: TextField

  @Ref()
  readonly suggestionsEl!: HTMLElement

  focused = false
  inputFocused = false
  suggestionsFocused = false

  showSuggestions = true // false

  inputText = ''

  suggestionsPromise: Promise<Suggestion[]> | null = null
  suggestions: Suggestion[] | null = null
  activeSuggestionIndex = 0

  viewportRect: DOMRect | null = null
  textFieldRect: DOMRect | null = null

  get diagram () {
    return this.diagramVm.diagram
  }

  get suggestionsStyle () {
    if (!this.viewportRect || !this.textFieldRect) return {}

    return {
      top: `${this.textFieldRect.bottom}px`,
      left: `${this.textFieldRect.left}px`,
      width: `${this.textFieldRect.width}px`,
      maxHeight: `${this.viewportRect.bottom - this.textFieldRect.bottom}px`,
    }
  }

  onDestroy?: () => void

  mounted () {
    const { unwatch: unwatchViewport } = watchViewport(rect => this.viewportRect = rect)
    const { unwatch: unwatchTextField } = watchRect(this.textField.$el as HTMLElement, rect => this.textFieldRect = rect)

    let ignoreFocusChange = false

    const handleFocusChange = () => {
      console.log('handleFocusChange')

      ignoreFocusChange = true

      const { inputFocused, suggestionsFocused } = this

      requestAnimationFrame(() => {
        console.log('handleFocusChange - raf - 1')

        if (
          this.inputFocused != inputFocused
          || this.suggestionsFocused != suggestionsFocused
        ) {
          console.log('handleFocusChange - raf - 2')

          handleFocusChange()
          return
        }

        console.log('handleFocusChange - raf - 3')

        ignoreFocusChange = false
        this.focused = this.inputFocused || this.suggestionsFocused
      })
    }

    this.$watch(() => [ this.inputFocused, this.suggestionsFocused ], () => {
      if (!ignoreFocusChange) handleFocusChange()
    })

    this.onDestroy = () => {
      unwatchViewport()
      unwatchTextField()
    }
  }

  beforeDestroy () {
    this.onDestroy?.()
  }

  async loadSuggestions () {
    const { entityType } = this

    const promise = (async (): Promise<Suggestion[]> => {
      let entities = this.diagram.activeDiagram.data.entities
      if (entityType) entities = entities.filter(e => e.type === entityType)

      return entities.map(entity => ({
        id: entity.id,
        name: entity.data.name,
      }))
    })()

    this.suggestionsPromise = promise

    const suggestions = await promise
    if (this.suggestionsPromise !== promise) return

    this.suggestionsPromise = null
    this.suggestions = suggestions
  }

  selectSuggestion (suggestion: Suggestion | null) {
    if (suggestion?.id !== this.entityId) this.$emit('change', suggestion?.id)
    this.inputText = suggestion?.name ?? ''
    this.showSuggestions = false
  }

  @Watch('entityType')
  onEntityTypeChanged () {
    this.suggestionsPromise = null
    this.suggestions = null
    this.loadSuggestions()
  }

  @Watch('inputText')
  onInputTextChanged () {

  }
}
