
































import { Vue, Component, Inject } from 'vue-property-decorator'
import Dialog from './Dialog.vue'
import EditorField from './EditorField.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import { createSavedDiagram, parseSavedDiagramJSON } from '@/types/SavedDiagram'
import DiagramVue from './Diagram.vue'
import { ActiveDiagram, Settings } from '@/diagram'
import DiagramData from '@/types/DiagramData'

@Component({
  components: {
    Dialog,
    EditorField,
    ButtonGroup,
    Button,
  },
})

export default class SaveLoadDialog extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  textareaText = ''

  get diagram () {
    return this.diagramVm.diagram
  }

  close () {
    this.$emit('close')
  }

  save () {
    const savedDiagram = createSavedDiagram(this.diagram)
    this.textareaText = JSON.stringify(savedDiagram, null, 2)
  }

  load () {
    const savedDiagram = parseSavedDiagramJSON(this.textareaText)
    if (!savedDiagram) return

    const activeDiagram = new ActiveDiagram(this.diagram)
    const { settings } = activeDiagram

    activeDiagram.id = savedDiagram.id
    activeDiagram.title = savedDiagram.title ?? ''
    activeDiagram.description = savedDiagram.description ?? ''
    activeDiagram.shared = savedDiagram.shared ?? false

    activeDiagram.data = new DiagramData(this.diagram, {
      data: savedDiagram.data,
      changes: savedDiagram.changes,
    })

    if (savedDiagram.settings) {
      if (savedDiagram.settings.activeFields) {
        for (const id in settings.activeFields) {
          settings.activeFields[id] = false
        }

        for (const id of savedDiagram.settings.activeFields) {
          if (id in settings.activeFields) settings.activeFields[id] = true
        }
      }

      if (savedDiagram.settings.activeFilters) {
        for (const id in settings.activeFilters) {
          settings.activeFilters[id] = false
        }

        for (const id of savedDiagram.settings.activeFilters) {
          if (id in settings.activeFilters) settings.activeFilters[id] = true
        }
      }

      if (savedDiagram.settings.activeLayout) {
        const id = savedDiagram.settings.activeLayout
        const layout = this.diagram.layouts.find(l => l.id === id)
        if (layout) settings.activeLayout = layout
      }
    }

    this.diagram.activeDiagram = activeDiagram
  }
}
